export interface IStringServiceInterface
{
    getString(): string
}

// A simple example of a service that depends on another service
export class ServiceA
{
    protected _stringService: IStringServiceInterface;
    
    constructor(stringService: IStringServiceInterface)
    {
        this._stringService = stringService;
    }
    
    public sayHello()
    {
        console.log('Message of injected service: ' + this._stringService.getString());
    }
}